import couponDetail from './child/couponDetail'
export default {
	components: {
		couponDetail
	},
	data() {
		return {
			couponVisible: 0,
			list: [],
			detail: [],
			currentCoupon: {},
			couponType: 0
		}
	},
	mounted() {
		this.getCoupon()
	},
	methods: {
		getCoupon() {
			return new Promise((resolve, reject) => {
				uni.showLoading()
				this.$http.post('coupon').then(res => {
					uni.hideLoading()
					this.list = res.data
					resolve(res)
				}).catch(e => {
					uni.hideLoading()
					reject(e)
				})
			})
		},
		showDetail(item) {
			if (item.coupon.type == 0) return
			let form = {
				coupon_id: item.coupon.id
			}
			this.currentCoupon = item
			this.couponType = item.coupon.type
			uni.showLoading()
			this.$http.post('coupon.detail', form).then(res => {
				uni.hideLoading()
				this.detail = res.data
				this.$refs.$popup.open()
			}).catch(e => { uni.hideLoading() })
		},
	},
	onPullDownRefresh() {
		this.getCoupon().then(e => {
			uni.stopPullDownRefresh()
		}).catch(e => {
			uni.stopPullDownRefresh()
		})
	}
	
}
